function menuImg() {
  const heroImg = document.createElement("img");
  heroImg.src = piazzaGalimbertiImg;

  return heroImg;
}

function menuHeadline() {
  const headline = document.createElement("h1");
  headline.textContent = "Menu";
  return headline;
}

function carousel(n_images = 4) {
  // import all images from carousel folder
  // const images = importAll(
  //   require.context("./images/carousel", false, /\.(png|jpe?g|svg)$/),
  // );
  const container = document.createElement("div");
  container.classList.add("carousel");

  const arr_carousel = new Array(n_images);
  for (let i = 0; i < n_images; i++) {
    let img = document.createElement("img");
    img.src = piazzaGalimbertiImg;
    container.appendChild(img);
    arr_carousel[i] = img;
  }

  // all images except three are invisible. The middle one is active
  arr_carousel[1].classList.add("img-active");
  arr_carousel[arr_carousel.length - 1].classList.add("img-non-active");

  // function button() {
  const button = document.createElement("button");
  button.addEventListener("click", function () {
    const imgs = container.querySelectorAll("img");
    for (let i = 0; i < imgs.length; i++) {
      if (imgs[i].classList.contains("img-active")) {
        imgs[i].classList.remove("img-active");
        imgs[(i + 1) % n_images].classList.remove("img-non-active");
        imgs[(i + 1) % n_images].classList.add("img-active");
        imgs[(i + 2) % n_images].classList.add("img-non-active");
        break;
      }
    }
  });

  // return heroImg;
  // }
  container.appendChild(button);
  // arr_carousel[0].setAttribute("margin-left","50%");

  // arr_carousel.forEach((i) => {

  // });

  // img.setAttribute("height", "50px");
  // img.setAttribute("width", "auto");
  return container;
}

function menuText() {
  const text = document.createElement("p");
  text.textContent = `
  This new restaurant has a lot of bagna cauda
  `;
  return text;
}

function renderMenu() {
  renderPage(carousel(), menuText());
}

export { renderMenu };
