___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --primary: #244653;
  --secondary: #1b313b;
}

body {
  background-color: rgb(150 255 145);
  display: flex;
  flex-direction: column;
  margin: 0;
}

nav {
  /* margin: 0 100px; */
  background-color: var(--primary);
  display: flex;
  justify-content: space-around;
  /* height: 50px; */
  width: 100%;
  padding: 10px 0;
}

nav button {
  /* font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; */
  border: none;
  background: none;
  /* margin: 0; */
  /* padding: 0; */
  color: white;
  width: 120px;
  height: 40px;
  border-radius: 10px;
  font-weight: 600;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

nav button:hover {
  background-color: var(--secondary);
}

#content {
  /* width: 100vw;
  height: 100vh; */
  /* border: 1px solid red; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

#content > img {
  width: 100%;
  margin: 0;
  padding: 0;
}

.carousel {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  width: 100%;
  gap: 100px;
  overflow: hidden;
  /* position:relative; */
}

.carousel button {
  width:100px;
  height:100px;

  position: absolute;
  top: 0;
  margin-left: 50%;
  margin-right: 50%;
  right: 0;
  bottom: 0;
  margin-right: auto;
}

.img-active {
  /* position:absolute; */
  height: 200px;
  /* display:none; */
  /* position:absolute; */
  /* top:0; */
  /* margin-left: 50%; */
  /* margin-right:50%; */
  /* right:0; */
  /* bottom:0; */
  /* margin-right:auto; */
  order:0;
}

.img-non-active {
  display:none;
}
